import { useState, useEffect, useCallback } from "react";
import { useParams, useNavigate } from "react-router-dom";
import { ModernTable } from "@/components/ModernTable";
import Header from "@/components/Header";
import Page from "@/components/Page";
import ErrorBoundary from "@/components/ErrorBoundary";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Sheet, SheetContent, SheetTrigger, SheetTitle, SheetDescription } from "@/components/ui/sheet";
import {
  Edit,
  Trash,
  Plus,
  Users,
  Calendar,
  Mail,
  Phone,
  ArrowLeft,
  UserPlus,
  AlertCircle,
  RefreshCw,
} from "lucide-react";
import { getContactsForList, getContactListById, addContactToList, updateContact, deleteContact } from "@/api/services/contact/contactService";
import { usePermissions } from "@/hooks/usePermissions";
import { toast } from "sonner";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import * as Yup from "yup";
import secureLocalStorage from 'react-secure-storage';

interface Contact {
  id: number;
  email: string;
  phoneNumber: string;
  fullName: string;
  createdAt: string;
}

interface ListInfo {
  id: number;
  listName: string;
  description: string;
  type: string;
  contactCount: number;
  createdAt: string;
  is_deleted?: boolean;
  deleted_at?: string;
}

interface ContactFormData {
  email: string;
  phoneNumber: string;
  fullName: string;
}

const contactValidationSchema = Yup.object().shape({
  email: Yup.string().required("Email is required").email("Email is invalid"),
  phoneNumber: Yup.string().required("Phone number is required"),
  fullName: Yup.string().required("Full name is required"),
});

const ListDetails = () => {
  const { id } = useParams<{ id: string }>();
  const navigate = useNavigate();
  const { isSuperAdmin } = usePermissions();
  const [listInfo, setListInfo] = useState<ListInfo | null>(null);
  const [contacts, setContacts] = useState<Contact[]>([]);
  const [loading, setLoading] = useState(true);
  const [operationLoading, setOperationLoading] = useState(false);
  const [lastOperationTime, setLastOperationTime] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(0);
  const [totalContacts, setTotalContacts] = useState(0);
  const [searchQuery, setSearchQuery] = useState("");
  const [editingContact, setEditingContact] = useState<Contact | null>(null);
  const [isAddingContact, setIsAddingContact] = useState(false);

  // Check authentication on component mount
  useEffect(() => {
    const checkAuth = () => {
      const token = secureLocalStorage.getItem('authToken') || secureLocalStorage.getItem('token');
      console.log("üîê Auth check - Token exists:", !!token);
      if (!token) {
        console.log("‚ùå No authentication token found");
        toast.error("Please log in to view list details");
        navigate("/login");
        return;
      }
    };

    checkAuth();
  }, [navigate]);

  const {
    register,
    handleSubmit,
    formState: { errors, isSubmitting },
    reset,
    setValue,
  } = useForm<ContactFormData>({
    resolver: yupResolver(contactValidationSchema),
    defaultValues: {
      email: "",
      phoneNumber: "",
      fullName: "",
    },
  });

  // Fetch list details and contacts (memoized to prevent unnecessary re-renders)
  const fetchListData = useCallback(async () => {
    if (!id) return;

    try {
      setLoading(true);

      // Add timeout to prevent hanging
      const timeoutPromise = new Promise((_, reject) =>
        setTimeout(() => reject(new Error('Request timeout')), 10000)
      );

      const dataPromise = Promise.all([
        getContactListById(id),
        getContactsForList(id, currentPage, searchQuery),
      ]);

      const result = await Promise.race([
        dataPromise,
        timeoutPromise
      ]);

      const [listResponse, contactsResponse] = result as [any, any];

      if (listResponse && listResponse.success) {
        const listData = listResponse.data;

        // Check if list is disabled and user is not super admin
        if (listData.is_deleted && !isSuperAdmin) {
          toast.error("This list has been disabled and is not accessible.");
          navigate('/lists');
          return;
        }

        setListInfo(listData);
      } else {
        // If list API fails but we have an ID, create a minimal list info
        // This allows the component to show the empty state with Add Contact button
        console.log("List API failed, creating minimal list info for ID:", id);
        setListInfo({
          id: parseInt(id),
          listName: `List ${id}`,
          description: "List details could not be loaded",
          type: "General",
          contactCount: 0,
          createdAt: new Date().toISOString()
        });
      }

      if (contactsResponse && contactsResponse.success) {
        setContacts(contactsResponse.data.contacts || []);
        setTotalContacts(contactsResponse.data.pagination?.totalContacts || 0);
        setTotalPages(contactsResponse.data.pagination?.totalPages || 1);
      } else {
        // If contacts API fails, just set empty array
        // This will show the empty state with Add Contact button
        console.log("Contacts API failed, showing empty state");
        setContacts([]);
        setTotalContacts(0);
        setTotalPages(1);
      }
    } catch (error: any) {
      console.error("Error fetching list data:", error);

      // More specific error handling
      if (error?.message === 'Request timeout') {
        toast.error("Request timed out. Please try again.");
      } else if (error?.response?.status === 401) {
        toast.error("Authentication failed. Please log in again.");
        navigate("/login");
        return; // Don't continue if auth failed
      } else if (error?.response?.status === 403) {
        // Handle disabled list access
        toast.error("This list has been disabled and is not accessible.");
        navigate("/lists");
        return;
      } else if (error?.response?.status >= 500) {
        toast.error("Server error. Please try again later.");
      } else {
        console.error("Failed to fetch list details");
      }

      // Instead of setting listInfo to null, create a minimal list info
      // This allows showing the empty state with Add Contact button
      console.log("API error, creating fallback list info for ID:", id);
      setListInfo({
        id: parseInt(id),
        listName: `List ${id}`,
        description: "Unable to load list details",
        type: "General",
        contactCount: 0,
        createdAt: new Date().toISOString()
      });

      setContacts([]);
      setTotalContacts(0);
      setTotalPages(1);
    } finally {
      // Ensure loading is always set to false
      setLoading(false);
    }
  }, [id, currentPage, searchQuery, navigate]); // Dependencies for useCallback

  // Separate function for refreshing data after operations (simplified to prevent hanging)
  const refreshContactsData = useCallback(async () => {
    if (!id) return;

    try {
      // Add timeout to prevent hanging
      const timeoutPromise = new Promise((_, reject) =>
        setTimeout(() => reject(new Error('Refresh timeout')), 5000)
      );

      const contactsPromise = getContactsForList(id, currentPage, searchQuery);

      const contactsResponse = await Promise.race([contactsPromise, timeoutPromise]);

      if (contactsResponse && contactsResponse.success) {
        setContacts(contactsResponse.data.contacts || []);
        setTotalContacts(contactsResponse.data.pagination?.totalContacts || 0);
        setTotalPages(contactsResponse.data.pagination?.totalPages || 1);
      }
    } catch (error: any) {
      console.error("Error refreshing contacts data:", error);
      // Don't show error toast for refresh failures, just log them
      // If refresh fails, don't update the UI to prevent inconsistent state
    }
  }, [id, currentPage, searchQuery]);

  useEffect(() => {
    fetchListData();
  }, [fetchListData]); // Use fetchListData as dependency since it's memoized

  // Handle adding new contact
  const handleAddContact = async (data: ContactFormData) => {
    if (!id || operationLoading) return;

    try {
      setOperationLoading(true);

      // Add timeout to prevent hanging
      const addPromise = addContactToList(id, {
        email: data.email,
        contact_number: data.phoneNumber,
        phoneNumber: data.phoneNumber,
        fullName: data.fullName,
      });

      const timeoutPromise = new Promise((_, reject) =>
        setTimeout(() => reject(new Error('Add timeout')), 15000)
      );

      await Promise.race([addPromise, timeoutPromise]);

      reset();
      setIsAddingContact(false);

      // Add the new contact to local state optimistically
      const newContact = {
        id: Date.now(), // Temporary ID
        email: data.email,
        phoneNumber: data.phoneNumber,
        fullName: data.fullName,
        createdAt: new Date().toISOString()
      };

      setContacts(prevContacts => [...prevContacts, newContact]);
      setTotalContacts(prev => prev + 1);

      toast.success("Contact added successfully");
    } catch (error: any) {
      console.error("Error adding contact:", error);

      // More specific error handling
      if (error?.message === 'Add timeout') {
        toast.error("Add timed out. Please check if the contact was added and try again.");
      } else if (error?.response?.status === 401) {
        toast.error("Authentication failed. Please log in again.");
      } else if (error?.response?.data?.error) {
        toast.error(`Add failed: ${error.response.data.error}`);
      } else {
        toast.error("Failed to add contact");
      }
    } finally {
      setOperationLoading(false);
    }
  };

  // Handle editing contact
  const handleEditContact = (contact: Contact) => {
    console.log("üîÑ handleEditContact called", { contact });

    // Add small delay to ensure any open dropdowns close first
    setTimeout(() => {
      setEditingContact(contact);
      setValue("email", contact.email);
      setValue("phoneNumber", contact.phoneNumber);
      setValue("fullName", contact.fullName || "");
    }, 100);
  };

  // Handle updating contact
  const handleUpdateContact = async (data: ContactFormData) => {
    console.log("üîÑ handleUpdateContact called", { editingContact, data, operationLoading });

    if (!editingContact || operationLoading) {
      console.log("‚ùå Update blocked:", { editingContact: !!editingContact, operationLoading });
      return;
    }

    // Prevent rapid successive operations
    const now = Date.now();
    if (now - lastOperationTime < 1000) {
      toast.error("Please wait a moment before performing another operation");
      return;
    }
    setLastOperationTime(now);

    try {
      console.log("‚úÖ Starting update operation for contact:", editingContact.id);
      setOperationLoading(true);

      // Add loading state for the update operation
      const updatePromise = updateContact(editingContact.id.toString(), {
        email: data.email,
        phoneNumber: data.phoneNumber,
        fullName: data.fullName,
      });

      // Add timeout to prevent hanging
      const timeoutPromise = new Promise((_, reject) =>
        setTimeout(() => reject(new Error('Update timeout')), 15000)
      );

      await Promise.race([updatePromise, timeoutPromise]);
      console.log("‚úÖ Update API call successful");

      // Store the contact ID before resetting
      const contactId = editingContact.id;
      console.log("üîÑ Updating local state for contact:", contactId);

      // Update local state optimistically BEFORE resetting form
      setContacts(prevContacts => {
        const updated = prevContacts.map(contact =>
          contact.id === contactId
            ? { ...contact, ...data }
            : contact
        );
        console.log("‚úÖ Local state updated successfully");
        return updated;
      });

      // Reset form state AFTER updating contacts
      reset();
      setEditingContact(null);
      console.log("‚úÖ Form reset and editing contact cleared");

      toast.success("Contact updated successfully");
    } catch (error: any) {
      console.error("Error updating contact:", error);

      // More specific error handling
      if (error?.message === 'Update timeout') {
        toast.error("Update timed out. Please check if the contact was updated and try again.");
      } else if (error?.response?.status === 401) {
        toast.error("Authentication failed. Please log in again.");
      } else if (error?.response?.data?.error) {
        toast.error(`Update failed: ${error.response.data.error}`);
      } else {
        toast.error("Failed to update contact");
      }

      // Don't reset form on error so user can retry
      // But ensure we don't leave the app in a broken state
      try {
        // Safely reset operation loading
        setOperationLoading(false);
      } catch (stateError) {
        console.error("Error resetting operation state:", stateError);
        // Force page reload as last resort
        window.location.reload();
      }
    } finally {
      // Ensure operation loading is always reset
      try {
        setOperationLoading(false);
      } catch (finalError) {
        console.error("Critical error in finally block:", finalError);
      }
    }
  };

  // Handle deleting contact
  const handleDeleteContact = async (contactId: number) => {
    if (!confirm("Are you sure you want to delete this contact?") || operationLoading) return;

    // Prevent rapid successive operations
    const now = Date.now();
    if (now - lastOperationTime < 1000) {
      toast.error("Please wait a moment before performing another operation");
      return;
    }
    setLastOperationTime(now);

    try {
      setOperationLoading(true);

      // Add timeout to prevent hanging
      const deletePromise = deleteContact(contactId.toString());
      const timeoutPromise = new Promise((_, reject) =>
        setTimeout(() => reject(new Error('Delete timeout')), 15000)
      );

      await Promise.race([deletePromise, timeoutPromise]);

      // Update local state optimistically instead of refetching
      setContacts(prevContacts => prevContacts.filter(contact => contact.id !== contactId));
      setTotalContacts(prev => Math.max(0, prev - 1));

      toast.success("Contact deleted successfully");
    } catch (error: any) {
      console.error("Error deleting contact:", error);

      // More specific error handling
      if (error?.message === 'Delete timeout') {
        toast.error("Delete timed out. Please check if the contact was deleted and try again.");
      } else if (error?.response?.status === 401) {
        toast.error("Authentication failed. Please log in again.");
      } else if (error?.response?.data?.error) {
        toast.error(`Delete failed: ${error.response.data.error}`);
      } else {
        toast.error("Failed to delete contact");
      }
    } finally {
      setOperationLoading(false);
    }
  };

  // Format date
  const formatDate = (dateString: string) => {
    const date = new Date(dateString);
    return new Intl.DateTimeFormat("en-US", {
      year: "numeric",
      month: "short",
      day: "numeric",
    }).format(date);
  };

  // Get badge variant based on list type
  const getBadgeVariant = (type: string) => {
    switch (type) {
      case "Marketing":
        return "default";
      case "Sales":
        return "secondary";
      case "Event":
        return "outline";
      case "Customer":
        return "destructive";
      default:
        return "default";
    }
  };

  // Contact actions
  const contactActions = [
    {
      label: "Edit Contact",
      icon: <Edit className="h-4 w-4" />,
      onClick: handleEditContact,
    },
    {
      label: "Delete Contact",
      icon: <Trash className="h-4 w-4" />,
      onClick: (contact: Contact) => handleDeleteContact(contact.id),
    },
  ];

  if (loading && !listInfo) {
    return (
      <Page>
        <div className="flex items-center justify-center h-64">
          <div className="text-center">
            <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary mx-auto mb-4"></div>
            <p className="text-gray-500">Loading list details...</p>
          </div>
        </div>
      </Page>
    );
  }

  if (!listInfo) {
    return (
      <Page>
        <div className="text-center p-8">
          <div className="h-16 w-16 bg-red-50 rounded-full flex items-center justify-center mx-auto mb-4">
            <AlertCircle className="h-8 w-8 text-red-500" />
          </div>
          <h3 className="text-lg font-medium mb-1">List not found</h3>
          <p className="text-gray-500 mb-4">
            The requested list could not be found. This might be due to:
          </p>
          <ul className="text-sm text-gray-500 mb-6 text-left max-w-md mx-auto">
            <li>‚Ä¢ The list may have been deleted</li>
            <li>‚Ä¢ You may not have permission to view this list</li>
            <li>‚Ä¢ There may be a connection issue</li>
          </ul>
          <div className="flex flex-col sm:flex-row gap-3 justify-center">
            <Button onClick={() => navigate("/lists")} variant="outline">
              <ArrowLeft className="h-4 w-4 mr-2" />
              Back to Lists
            </Button>
            <Button onClick={() => window.location.reload()}>
              <RefreshCw className="h-4 w-4 mr-2" />
              Retry
            </Button>
          </div>
        </div>
      </Page>
    );
  }

  return (
    <ErrorBoundary>
      <Page>
      {/* Header with back button */}
      <div className="flex items-center justify-between mb-6">
        <div className="flex items-center space-x-4">
          <Button
            variant="outline"
            size="sm"
            onClick={() => navigate("/lists")}
            className="flex items-center space-x-2"
          >
            <ArrowLeft className="h-4 w-4" />
            <span>Back to Lists</span>
          </Button>
          <div>
            <h1 className="text-2xl font-bold text-gray-900">{listInfo.listName}</h1>
            <p className="text-gray-600">{listInfo.description}</p>
          </div>
        </div>
        <div className="flex items-center space-x-3">
          {operationLoading && (
            <div className="flex items-center space-x-2 text-blue-600">
              <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-blue-600"></div>
              <span className="text-sm">Processing...</span>
            </div>
          )}
          <Badge variant={getBadgeVariant(listInfo.type)} className="capitalize">
            {listInfo.type}
          </Badge>
          <Badge variant="secondary">
            <Users className="h-3 w-3 mr-1" />
            {totalContacts} Contacts
          </Badge>
        </div>
      </div>

      {/* Search and Add Contact */}
      <div className="flex flex-col sm:flex-row items-start sm:items-center justify-between mb-6 gap-3">
        <div className="flex-1 max-w-md">
          <Input
            placeholder="Search contacts..."
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            className="w-full"
          />
        </div>
        
        <Sheet
          open={isAddingContact || !!editingContact}
          onOpenChange={(open) => {
            console.log("üîÑ Sheet onOpenChange", { open, isAddingContact, editingContact: !!editingContact });
            if (!open) {
              setIsAddingContact(false);
              setEditingContact(null);
              reset();
            }
          }}
        >
          <SheetTrigger asChild>
            <Button
              onClick={() => setIsAddingContact(true)}
              className="flex items-center space-x-2"
            >
              <Plus className="h-4 w-4" />
              <span>Add Contact</span>
            </Button>
          </SheetTrigger>
          <SheetContent
            size="md"
            onOpenAutoFocus={(event) => {
              // Prevent auto-focus issues when sheet opens
              event.preventDefault();
            }}
            onCloseAutoFocus={(event) => {
              // Prevent auto-focus issues when sheet closes
              event.preventDefault();
            }}
          >
            <SheetTitle>
              {editingContact ? "Edit Contact" : "Add New Contact"}
            </SheetTitle>
            <SheetDescription>
              {editingContact
                ? "Update the contact information below."
                : "Add a new contact to this list."
              }
            </SheetDescription>

            <form
              onSubmit={handleSubmit(editingContact ? handleUpdateContact : handleAddContact)}
              className="space-y-4 mt-6"
            >
              <div>
                <Label htmlFor="fullName">Full Name *</Label>
                <Input
                  id="fullName"
                  placeholder="Enter full name"
                  {...register("fullName")}
                />
                {errors.fullName && (
                  <p className="text-sm text-red-600 mt-1">{errors.fullName.message}</p>
                )}
              </div>

              <div>
                <Label htmlFor="email">Email *</Label>
                <Input
                  id="email"
                  type="email"
                  placeholder="Enter email address"
                  {...register("email")}
                />
                {errors.email && (
                  <p className="text-sm text-red-600 mt-1">{errors.email.message}</p>
                )}
              </div>

              <div>
                <Label htmlFor="phoneNumber">Phone Number *</Label>
                <Input
                  id="phoneNumber"
                  placeholder="Enter phone number"
                  {...register("phoneNumber")}
                />
                {errors.phoneNumber && (
                  <p className="text-sm text-red-600 mt-1">{errors.phoneNumber.message}</p>
                )}
              </div>

              <div className="flex justify-end space-x-3 pt-4">
                <Button
                  type="button"
                  variant="outline"
                  onClick={() => {
                    setIsAddingContact(false);
                    setEditingContact(null);
                    reset();
                  }}
                >
                  Cancel
                </Button>
                <Button type="submit" disabled={isSubmitting}>
                  {isSubmitting 
                    ? (editingContact ? "Updating..." : "Adding...") 
                    : (editingContact ? "Update Contact" : "Add Contact")
                  }
                </Button>
              </div>
            </form>
          </SheetContent>
        </Sheet>
      </div>

      {/* Contacts Table */}
      <ModernTable
        data={contacts}
        isSelectable={true}
        showColumnSelection={true}
        columns={[
          {
            header: "Contact",
            accessor: "email",
            className: "min-w-[250px]",
            cell: (value, item) => (
              <div className="flex items-center space-x-3">
                <div className="h-9 w-9 bg-gradient-to-br from-primary/20 to-primary/10 rounded-lg flex items-center justify-center">
                  <Mail className="h-5 w-5 text-primary" />
                </div>
                <div>
                  <div className="font-medium text-gray-900">
                    {item.fullName ? `${item.fullName}` : "No Name"}
                  </div>
                  <div className="text-sm text-gray-500">{String(value)}</div>
                </div>
              </div>
            ),
            sortable: true,
          },
          {
            header: "Phone Number",
            accessor: "phoneNumber",
            className: "min-w-[150px]",
            cell: (value) => (
              <div className="flex items-center space-x-2">
                <Phone className="h-4 w-4 text-gray-400" />
                <span className="text-gray-700">{String(value)}</span>
              </div>
            ),
            sortable: true,
          },
          {
            header: "Added",
            accessor: "createdAt",
            className: "min-w-[150px]",
            cell: (value) => (
              <div className="flex items-center space-x-2">
                <Calendar className="h-4 w-4 text-gray-400" />
                <span className="text-gray-700">{formatDate(String(value))}</span>
              </div>
            ),
            sortable: true,
          },
        ]}
        actions={contactActions}
        emptyState={
          <div className="text-center p-12">
            <div className="h-20 w-20 bg-gradient-to-br from-primary/20 to-primary/10 rounded-full flex items-center justify-center mx-auto mb-6">
              <UserPlus className="h-10 w-10 text-primary" />
            </div>
            <h3 className="text-xl font-semibold mb-2">
              {loading ? "Loading contacts..." : "No contacts"}
            </h3>
            <p className="text-gray-500 mb-6 max-w-md mx-auto">
              {loading
                ? "Please wait while we fetch the contacts"
                : searchQuery
                  ? "No contacts match your search criteria."
                  : "This list is empty. Add your first contact to get started."
              }
            </p>
            {!loading && (
              <div className="space-y-3">
                <Button
                  onClick={() => setIsAddingContact(true)}
                  className="flex items-center space-x-2 px-6 py-3"
                  size="lg"
                >
                  <Plus className="h-5 w-5" />
                  <span>Add Contact</span>
                </Button>
                {!searchQuery && (
                  <p className="text-xs text-gray-400">
                    You can add contacts manually or import them from a CSV file
                  </p>
                )}
              </div>
            )}
          </div>
        }
      />

      {/* Pagination */}
      {totalPages > 1 && (
        <div className="mt-8 border-t border-gray-200 pt-4">
          <div className="flex items-center justify-between">
            <div className="text-sm text-gray-700">
              Showing {((currentPage - 1) * 10) + 1} to {Math.min(currentPage * 10, totalContacts)} of {totalContacts} contacts
            </div>
            <div className="flex items-center space-x-2">
              <Button
                variant="outline"
                size="sm"
                onClick={() => setCurrentPage(prev => Math.max(1, prev - 1))}
                disabled={currentPage === 1}
              >
                Previous
              </Button>
              <span className="text-sm text-gray-700">
                Page {currentPage} of {totalPages}
              </span>
              <Button
                variant="outline"
                size="sm"
                onClick={() => setCurrentPage(prev => Math.min(totalPages, prev + 1))}
                disabled={currentPage === totalPages}
              >
                Next
              </Button>
            </div>
          </div>
        </div>
      )}
    </Page>
    </ErrorBoundary>
  );
};

export default ListDetails;
